body {
  --active-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo_container {
  display: flex;
}

.nav_container {
  height: 100%;
  position: relative;
}

.nav_container__list {
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

.list_item {
  align-items: center;
  height: 60px;
  margin: 0 10px;
  list-style: none;
  display: flex;
}

.list_item__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  text-decoration: none;
}

.list_item:first-child {
  margin-left: 0;
}

.is-active {
  color: var(--active-color);
  align-items: center;
  height: 100%;
  display: flex;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.list_item:last-child {
  margin-right: 0;
}

.list_item__link:hover {
  color: var(--active-color);
}

